/* Default Button */
.button {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  font-size: 16px;
  color: #fff;
  background-color: var(--primaryBlue);
  min-width: 150px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  overflow: hidden;
  transition: all 300ms ease-in-out;
  text-transform: capitalize;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  margin-top: 20px;
}
.button:hover {
  background-color: var(--primaryBlue);
  opacity: 0.8;
  outline: none;
}

.button:focus {
  background-color: var(--primaryBlue);
  border: 1px solid #ffffff;

  outline: none;
}

/* Default Button with Outline */
.button-outline {
  background-color: transparent;
  color: var(--primaryBlue);
  border: 1px solid var(--primaryBlue);
}
.button-outline:hover {
  background-color: var(--blue);
  color: white;
}

.button-outline:active {
  background-color: var(--primaryBlue);
  color: white;
}

/* Disabled Button */
.button-disabled {
  background-color: #e0e0e0;
  border: none;
  color: #828282;
  cursor: default;
  pointer-events: none;
}

/* Cancel Button */
.button-cancel {
  background-color: #e0e0e0;
  border: none;
  color: #828282;
  cursor: pointer;
}

.button-cancel:hover {
  background-color: #e0e0e0;
  border: none;
  color: #828282;
  cursor: pointer;
}

.button-cancel:focus {
  background-color: #e0e0e0;
  border: none;
  color: #828282;
  cursor: pointer;
}

