.create-event-main-container {
  height: calc(var(--vh, 1vh) * 95);
  width: 100vw;
  display: flex;
  flex-direction: row;
  background-color: #fff;
  padding: 20px;
  min-height: 670px;
  overflow: scroll;
}
.create-event-header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.create-event-header > p {
  font-size: 18px;
}

.create-event-header > img {
  width: 100px;
  height: 100px;
}

.view-button {
  color: var(--primaryBlue);
  padding: 20px 0px 0px 40px;
  text-decoration: underline;
  cursor: pointer;
}

.create-event-left-container {
  width: 50vw;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.create-event-right-container {
  height: 100%;
  width: 50vw;
  position: relative;
  border-radius: 20px;
  background-color: #f3f4f7;
  backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 10px 20px;
}

.free-slots-container {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  width: 100%;
  padding: 20px;
}

.free-slots-container > p {
  font-size: 16px;
  color: #333;
  border: 1px solid #e0e0e0;
  padding: 15px;
  background: #fff;
  border-radius: 4px;
  cursor: pointer;
}

.row-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
}

.react-calendar {
  border: 1px solid #e0e0e0;
  border-radius: 4px;
  width: 50%;
}

.duration-textfield-container {
  width: 50%;
  margin: 20px 0px;
}

.timezone-dropdown-container {
  width: 50%;
}

.timezone-dropdown-container > p {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
}

.select-slot-header {
  font-size: 20px;
  color: #333;
  padding: 20px;
}

.Dropdown-control {
  padding: 15px 52px 15px 10px;
  border-radius: 4px;
  border: 1px solid #e0e0e0;
  cursor: pointer;
}

.Dropdown-arrow {
  top: 23px;
}

.react-calendar__tile--active {
  border-radius: 4px;
}

@media only screen and (max-width: 768px) {
  .create-event-main-container {
    flex-direction: column;
    gap: 20px;
  }

  .create-event-left-container,
  .create-event-right-container {
    width: 90vw;
  }

  .react-calendar,
  .duration-textfield-container,
  .timezone-dropdown-container {
    width: 100%;
  }
}
