.get-event-main-container {
  height: calc(var(--vh, 1vh) * 100);
  width: 100vw;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  padding: 20px 30px;
  min-height: 670px;
  overflow: scroll;
}

.back-button {
  color: var(--primaryBlue);
  padding-bottom: 20px;
  text-decoration: underline;
  cursor: pointer;
}

.error-msg {
  padding-top: 20px;
  color: red;
}

.range-select-container {
  width: 50vw;
  display: flex;
  align-items: flex-start;
  flex-direction: row;
  align-items: center;
  gap: 20px;
}

.range-select-container .button {
  margin-top: 35px;
}

.start-date-container,
.end-date-container {
  width: 40%;
}

.event-list-main-container {
  margin: 40px 0px;
}

.event-list-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  gap: 20px;
}

.single-event-container {
  padding: 20px;
  border: 1px solid #e0e0e0;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
  width: 200px;
  border-radius: 8px;
}

.single-event-container > p {
  color: #333;
  font-size: 14px;
  padding: 10px 0px;
}

.Dropdown-control {
  padding: 10px 52px 10px 10px;
  border-radius: 4px;
  border: 1px solid #e0e0e0;
  cursor: pointer;
}

.timezone-dropdown-container {
  width: 50%;
  margin-top: 5px;
}

.Dropdown-arrow {
  top: 18px;
}

.timezone-dropdown-container > p {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
}

@media only screen and (max-width: 500px) {
  .single-event-container {
    width: 150px;
  }

  .single-event-container > p {
    color: #333;
    font-size: 12px;
    padding: 10px 0px;
  }

  .event-list-container {
    justify-content: center;
  }
}
